body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loginBG{
    height: 100%;
    width: 100%;
    background: url(../../static/media/loginBG.0899ffa6.jpg) no-repeat;
    background-size: 100% 100%;
}

.login-con{
    position: absolute;
    right: 160px;
    top: 50%;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
    width: 300px;
}

.ivu-card{
    background: #fff;
    border-radius: 4px;
    font-size: 14px;
    position: relative;
    transition: all .2s ease-in-out;
}

.ivu-card-head{
    border-bottom: 1px solid #e8eaec;
    padding: 14px 16px;
    line-height: 1;
    box-sizing: border-box;
}

.ivu-card-head-inner, .ivu-card-head p{
    display: inline-block;
    width: 100%;
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    color: #17233d;
    font-weight: 700;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ivu-card-head p i, .ivu-card-head p span{
    vertical-align: middle;
}

.ivu-card-body{
    padding: 16px;
}

.login-con .form-con{
    padding: 10px 0 0;
}

.login-con .login-tip{
    font-size: 10px;
    text-align: center;
    color: #c3c3c3;
}
.mainBox{
    height: 100%;
    display: flex;
    flex-direction: row;
    flex:auto;
    background:#f5f7f9;
}

.mainBox .rightSider{
    transition: all .2s ease;
}

.mainBox .header-con{
    background: #fff;
    padding: 0 20px;
    width: 100%;
    height: 64px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.mainBox .header-bar{
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.mainBox .sider-a{
    padding: 6px;
    line-height: 0;
}

.mainBox .sider-a i{
    font-size: 26px;
    color: #5c6b77;
}

.mainBox .crumb{
    margin-left:30px;
}

.mainBox .custom-con{
    width: 64px;
    height: 100%;
    border-radius: 32px;
    margin-right: 30px;
}
custom-con
.mainBox .avatar{
    display: flex;
    flex-direction: row;
    align-items: center;
    flex:1 1;
}

.mainBox .custom-con img{ width: 32px;height: 32px;border-radius: 32px;margin-right: 5px;}

.mainBox .tagBox{ 
    width: 100%;
    height: 40px;
    background: #f0f0f0;
}

.mainBox .tags-nav{
    position: relative;
    border-top: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    height: 40px;
}

.mainBox .btn-con{
    position: absolute;
    top: 0;
    height: 100%;
    background: #fff;
    z-index:10;
    line-height: 40px;
    padding: 0 5px;
    cursor: pointer;
}

.mainBox .left-btn{ left: 0}
.mainBox .right-btn{ right: 0;}

.mainBox .scroll-outer{
    position: absolute;
    left: 24px;
    right: 24px;
    top:0;
    bottom: 0;
    box-shadow: inset 0 0 3px 2px hsla(0,0%,39.2%,.1);
}

.mainBox .scroll-body{
    height: 100%;
    display: inline-block;
    padding: 1px 4px 0;
    position: absolute;
    overflow: visible;
    white-space: nowrap;
    transition: all .3s ease;
}

.mainBox .tag{ 
    height: 32px;
    line-height: 32px;
    border: 1px solid #e8eaec!important;
    background: #fff!important;
    padding: 0 12px;
    display: inline-block;
    margin: 2px 4px 2px 0;
    font-size: 12px;
    vertical-align: middle;
    opacity: 1;
    overflow: hidden;
    cursor: pointer;
    border-radius: 3px;
}

.mainBox .tag-dot-inner{
    transition: background .2s ease;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-right: 8px;
    border-radius: 50%;
    background: #e8eaec;
    position: relative;
    top: 1px;
}

.mainBox .tag-primay .tag-dot-inner{
    background: #2d8cf0;
}

.mainBox .tag-text{
    color: #515a6e;
    box-sizing: border-box;
}

.mainBox #content{
    width: 100%;
    height: calc(100% - 102px);
    
}

.App , #root , html , body{ width: 100%;height: 100%;margin: 0;padding: 0;}

article, aside, blockquote, body, button, dd, details, div, dl, dt, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, input, legend, li, menu, nav, ol, p, section, td, textarea, th, ul{ padding: 0;margin: 0;}

/*公共*/
.container{ width: 100%;height: 100%;overflow-y: auto;padding: 15px;}
.displayNone{ display: none;}
.tableContentBox{ background: #fff;height: 300px;border-radius:5px;border:1px solid #dcdee2;padding: 15px;box-sizing: border-box;}

/*菜单*/
.menuBox{ height: 100%;}
.menuBox .logo-con{ height: 64px;padding: 10px;text-align: center}
.menuBox .logo-con img{ height: 44px;width: auto;margin: 0 auto;}
.menuBox .side-menu{ box-sizing:border-box;}

/*首页*/
.home .homeBox{ width: 100%; padding: 20px 15px;color: #fff;font-size: 26px;background: #428bca;text-align: center;border-radius: 5px;}
.home .homeBox i{ margin-right: 20px;font-size: 50px;position: relative;top: 5px;}
.home .homeBox span{ display: inline-block;height: 50px;line-height: 50px;position: relative;top: -5px;}

/*搜索*/
.searchBox .ant-form-item{ margin-bottom: 0;}
.searchBox .ant-btn{ margin-bottom: 8px;}

/*antd样式*/
.ant-card-wider-padding .ant-card-body{ padding: 24px;}
.ant-table-thead > tr > th, .ant-table-tbody > tr > td{ padding: 12px 10px;}

/*新增样式*/
.addTitle{ font-size: 16px;font-weight: bold;border-bottom:1px solid #ececec;padding-bottom:10px;margin-bottom:10px;}
.addLabel{ padding-top: 10px;}

/*支付信息模块*/
.payTitle{ padding:10px 0;border-bottom:1px solid #ececec;margin-top:10px;margin-bottom:5px;}
.payRow{ padding:10px 0;}

