.loginBG{
    height: 100%;
    width: 100%;
    background: url('../images/loginBG.jpg') no-repeat;
    background-size: 100% 100%;
}

.login-con{
    position: absolute;
    right: 160px;
    top: 50%;
    transform: translateY(-60%);
    width: 300px;
}

.ivu-card{
    background: #fff;
    border-radius: 4px;
    font-size: 14px;
    position: relative;
    transition: all .2s ease-in-out;
}

.ivu-card-head{
    border-bottom: 1px solid #e8eaec;
    padding: 14px 16px;
    line-height: 1;
    box-sizing: border-box;
}

.ivu-card-head-inner, .ivu-card-head p{
    display: inline-block;
    width: 100%;
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    color: #17233d;
    font-weight: 700;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ivu-card-head p i, .ivu-card-head p span{
    vertical-align: middle;
}

.ivu-card-body{
    padding: 16px;
}

.login-con .form-con{
    padding: 10px 0 0;
}

.login-con .login-tip{
    font-size: 10px;
    text-align: center;
    color: #c3c3c3;
}