.mainBox{
    height: 100%;
    display: flex;
    flex-direction: row;
    flex:auto;
    background:#f5f7f9;
}

.mainBox .rightSider{
    transition: all .2s ease;
}

.mainBox .header-con{
    background: #fff;
    padding: 0 20px;
    width: 100%;
    height: 64px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.mainBox .header-bar{
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.mainBox .sider-a{
    padding: 6px;
    line-height: 0;
}

.mainBox .sider-a i{
    font-size: 26px;
    color: #5c6b77;
}

.mainBox .crumb{
    margin-left:30px;
}

.mainBox .custom-con{
    width: 64px;
    height: 100%;
    border-radius: 32px;
    margin-right: 30px;
}
custom-con
.mainBox .avatar{
    display: flex;
    flex-direction: row;
    align-items: center;
    flex:1;
}

.mainBox .custom-con img{ width: 32px;height: 32px;border-radius: 32px;margin-right: 5px;}

.mainBox .tagBox{ 
    width: 100%;
    height: 40px;
    background: #f0f0f0;
}

.mainBox .tags-nav{
    position: relative;
    border-top: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    height: 40px;
}

.mainBox .btn-con{
    position: absolute;
    top: 0;
    height: 100%;
    background: #fff;
    z-index:10;
    line-height: 40px;
    padding: 0 5px;
    cursor: pointer;
}

.mainBox .left-btn{ left: 0}
.mainBox .right-btn{ right: 0;}

.mainBox .scroll-outer{
    position: absolute;
    left: 24px;
    right: 24px;
    top:0;
    bottom: 0;
    box-shadow: inset 0 0 3px 2px hsla(0,0%,39.2%,.1);
}

.mainBox .scroll-body{
    height: 100%;
    display: inline-block;
    padding: 1px 4px 0;
    position: absolute;
    overflow: visible;
    white-space: nowrap;
    transition: all .3s ease;
}

.mainBox .tag{ 
    height: 32px;
    line-height: 32px;
    border: 1px solid #e8eaec!important;
    background: #fff!important;
    padding: 0 12px;
    display: inline-block;
    margin: 2px 4px 2px 0;
    font-size: 12px;
    vertical-align: middle;
    opacity: 1;
    overflow: hidden;
    cursor: pointer;
    border-radius: 3px;
}

.mainBox .tag-dot-inner{
    transition: background .2s ease;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-right: 8px;
    border-radius: 50%;
    background: #e8eaec;
    position: relative;
    top: 1px;
}

.mainBox .tag-primay .tag-dot-inner{
    background: #2d8cf0;
}

.mainBox .tag-text{
    color: #515a6e;
    box-sizing: border-box;
}

.mainBox #content{
    width: 100%;
    height: calc(100% - 102px);
    
}
